
const notebookPipeline = {
  state: {
    pipeline: []
  },
  mutations: {
    addIntoPipeline(state, payload){
      state.pipeline.push({
        func: payload.func,
        label: payload.label,
        form: payload.form
      })
    },
    removeFromPipeline(state, payload){
      let id = payload.id;
      state.pipeline.remove(id);
      // let p = [];
      // throw "Undefined Exception"
    }
  }
}

export default notebookPipeline;


