const functions = {
  import: {
    label: "导入",
    icon: "el-icon-upload",
    form: [
      {label: "文件", type: "file", params: {comment: "只能上传stl文件"}}
    ]
  },
  connect: {
    label: "连接",
    icon: "el-icon-coordinate",
    form: [
      {label: "阈值", type: "input", params: {"type": "number"}}
    ]
  },
  export: {
    label: "导出",
    icon: "el-icon-download",
    form: [
      {label: "格式", type: "select", params: {default: "format1", options: [["format1", "格式1"], ["format2", "格式2"]]}}
    ]
  },
  view1: {
    label: "视图1",
    icon: "el-icon-attract",
    form: [
      {label: "阈值", type: "input", params: {"type": "number"}}
    ]
  },

  modeling1: {
    label: "建模",
    icon: "el-icon-shopping-bag-1",
    form: []
  },

  simulation1: {
    label: "仿真1",
    icon: "el-icon-s-promotion",
    form: [
      {label: "类型", type: "select", params: {default: "type1", options: [["type1", "类型1"], ["type2", "类型2"]]}}
    ]
  }

}

const functionList = {
  tabs: [
    {name: "IO", label: "IO", functions: ['import', 'connect', 'export']},
    {name: "View", label: "视图", functions: ['view1']},
    {label: "建模", functions: ['modeling1']},
    {label: "仿真", functions: ['simulation1']},
  ],
  functions

}

export default functionList;


