<template>
  <el-container id="model-area"
                v-loading="loading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <VueDraggableResize id="model-stats" :w="80" :h="48"
                        :y="80" :x="statsPositionX"
                        :handle-info="{size: 0,offset: 0,switch: true}"
                        :isResizable="false">
    </VueDraggableResize>
  </el-container>
</template>

<script>
import * as THREE from "three";
import VueDraggableResize from 'vue-draggable-resizable-gorkys'

let STLLoader = require('three-stl-loader')(THREE);
let OrbitControls = require('three-orbit-controls')(THREE);
let Stats = require('stats.js');
export default {
  name: "Model",
  components: {VueDraggableResize},
  data() {
    return {
      loading: true,
      camera: null,
      cameraTarget: null,
      scene: null,
      renderer: null,
      axisHelper: null,
      stats: null,
      mesh: null,
      dummy: new THREE.Object3D()
    }
  },
  computed: {
    statsPositionX() {
      return window.innerWidth - 80 - 20
    }
  },
  mounted() {
    this.init();
    // this.loadStlObject();
    // this.loadJSONObject();
    // this.loadInstancedObject();
    // this.loadManualInstancedObject();
    this.loadMultiInstancedObject();
    this.createLights();
    this.animate();
    this.actionInit();
  },
  methods: {
    init() {
      let container = document.getElementById("model-area");
      // this.camera = new THREE.PerspectiveCamera(35, window.innerWidth / (window.innerHeight - 60), 1, 15);
      this.camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 150);
      // this.camera.position.set(3, 0.15, 3);
      this.camera.position.set(30, 1.5, 30);
      this.cameraTarget = new THREE.Vector3(0, -0.25, 0);

      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0x555555);
      // this.scene.fog = new THREE.Fog(0x555555, 2, 15);

      this.renderer = new THREE.WebGLRenderer({antialias: true});
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(window.innerWidth, window.innerHeight - 60);
      this.renderer.outputEncoding = THREE.sRGBEncoding;

      this.renderer.shadowMap.enabled = true;

      container.appendChild(this.renderer.domElement);

      window.addEventListener('resize', this.onWindowResize, false);

      let controls = new OrbitControls(this.camera, this.renderer.domElement);//创建控件对象
      controls.addEventListener('change', this.render);//监听鼠标、键盘事件

      this.axisHelper = new THREE.AxisHelper(0.15);
      this.scene.add(this.axisHelper);

      this.stats = new Stats();  // 创建一个性能监视器
      this.stats.domElement.style.position = 'absolute';  // 样式， 坐标
      console.log(container)
      document.getElementById('model-stats').appendChild(this.stats.domElement);
    },
    make_center(geometry) {
      var cols = geometry.attributes.position.itemSize;
      var rows = geometry.attributes.position.count;
      var mean = Array(cols).fill(0);
      for (let i = 0; i < rows; ++i) {
        for (let j = 0; j < cols; ++j) {
          mean[j] += geometry.attributes.position.array[i * cols + j];
        }
      }
      for (let j = 0; j < cols; ++j) {
        mean[j] /= rows;
      }
      geometry.computeBoundingBox();
      var arr = geometry.boundingBox.max.clone().sub(geometry.boundingBox.min.clone());
      var scale = Math.max(...[arr.x, arr.y, arr.z]);

      for (let i = 0; i < rows; ++i) {
        for (let j = 0; j < cols; ++j) {
          geometry.attributes.position.array[i * cols + j] -= mean[j];
          geometry.attributes.position.array[i * cols + j] /= scale;
        }
      }
      // geometry.attributes.position.array.map(x => x / scale);
      geometry.computeBoundingBox();
    },
    loadJSONObject() {
      this.$http.get("/static/models/colormap.json").then(response => {
        let faces = response.data["mesh"];
        let nodes = response.data["nodes"];
        let colors = response.data["colors"];
        let normals = response.data["normals"];
        console.log("faces: ", faces.length);
        console.log("nodes: ", nodes.length);
        console.log("colors: ", colors.length);
        let geometry = new THREE.Geometry();
        for (let i in nodes) {
          let node = nodes[i];
          geometry.vertices.push(new THREE.Vector3(node[0], node[1], node[2]));
        }
        geometry.colorsNeedUpdate = true;
        for (let i in faces) {
          let face = faces[i];
          let normal = normals[i];
          let f = new THREE.Face3(face[0], face[1], face[2]);
          f.normal = new THREE.Vector3(normal[0], normal[1], normal[2]);
          f.vertexColors = [new THREE.Color(0xff0000), new THREE.Color(0xff0000), new THREE.Color(0xff0000)];
          f.vertexColors[0].setRGB(colors[face[0]], 0.4, 1 - colors[face[0]]);
          f.vertexColors[1].setRGB(colors[face[1]], 0.4, 1 - colors[face[1]]);
          f.vertexColors[2].setRGB(colors[face[2]], 0.4, 1 - colors[face[2]]);

          geometry.faces.push(f);
        }

        geometry.colorsNeedUpdate = true;
        geometry.elementsNeedUpdate = true;
        geometry.computeBoundingSphere();
        // let material = new THREE.MeshBasicMaterial({
        //   color: 0xffffff,
        //   shading: THREE.FlatShading,
        //   vertexColors: THREE.VertexColors
        // });

        geometry = new THREE.BufferGeometry().fromGeometry(geometry);

        let material = new THREE.MeshPhongMaterial({
          metalness: 1.0,
          roughness: 0.6,
          vertexColors: THREE.VertexColors,
        });
        console.log(material);
        let mesh = new THREE.Mesh(geometry, material);
        mesh.position.set(0, 0, 0);
        mesh.rotation.set(0, -Math.PI / 2, 0);
        mesh.scale.set(2, 2, 2);

        mesh.castShadow = true;
        mesh.receiveShadow = true;
        console.log(mesh);
        this.scene.add(mesh);
        this.loading = false;
      })
    },
    loadStlObject() {
      new STLLoader().load('./static/models/Deform_Model_2.stl', geometry => {
        this.make_center(geometry);
        console.log(geometry)
        geometry = new THREE.Geometry().fromBufferGeometry(geometry);
        // console.log(geometry);

        let material = new THREE.MeshPhongMaterial({color: 0x777777, specular: 0, shininess: 20});
        let mesh = new THREE.Mesh(geometry, material);
        mesh.position.set(0, 0, 0);
        mesh.rotation.set(0, -Math.PI / 2, 0);
        mesh.scale.set(2, 2, 2);

        mesh.castShadow = true;
        mesh.receiveShadow = true;
        console.log(mesh);
        this.scene.add(mesh);
        this.loading = false;

        return;

        this.$http.get("/static/models/Z3.color.json")
            .then(response => {
              // let vertex = response.data;
              // console.log(vertex);
              // make color
              let colors = response.data;
              // let colors = [];
              // let cols = geometry.attributes.position.itemSize;
              // let rows = geometry.attributes.position.count;

              // for (let i = 0; i < rows; ++i) {
              //   // let color = [];
              //   colors.push(0.5+geometry.attributes.position.array[i * cols + 0]);
              //   for (let j = 1; j < cols; ++j) {
              //     colors.push(0.5+geometry.attributes.position.array[i * cols + 0]);
              //   }
              // }
              // console.log(rows)
              // geometry.colors = colors;

              // geometry.addAttribute('color', new THREE.BufferAttribute(new Float32Array(colors), 3));
              // geometry.hasColors = true;
              // geometry.alpha = 1;

              // console.log(geometry)

              // let material = new THREE.MeshPhongMaterial({color: 0x777777, specular: 0, shininess: 20});
              // let material = new THREE.MeshBasicMaterial({vertexColors: THREE.VertexColors});
              // let material = new THREE.MeshPhongMaterial({
              //   vertexColors: THREE.VertexColors,
              //   specular: 0,
              //   shininess: 20
              // });


            });
      });
    },
    loadInstancedObject() {
      const urlParams = new URLSearchParams(window.location.search);
      const cntp = urlParams.get('count');

      let count = parseInt(cntp) || 10000;
      let geometry = new THREE.CylinderBufferGeometry(0.2, 0.2, 1.5, 10, 10);
      // let material = new THREE.MeshPhongMaterial({color: 0x777777, specular: 0, shininess: 20});
      let material = new THREE.MeshPhysicalMaterial({color: 0x777777})
      // let material = new THREE.MeshNormalMaterial();
      // let mesh = new THREE.Mesh(geometry, material);

      this.mesh = new THREE.InstancedMesh(geometry, material, count);
      this.mesh.instanceMatrix.setUsage(THREE.StaticReadUsage);

      this.mesh.position.set(0, 0, 0);
      this.mesh.rotation.set(0, -Math.PI / 2, 0);
      this.mesh.scale.set(1, 1, 1);

      this.mesh.castShadow = true;
      this.mesh.receiveShadow = true;

      this.scene.add(this.mesh);

      let amount = Math.ceil(Math.pow(count, 1 / 3));

      let time = Date.now() * 0.01;
      this.mesh.rotation.x = Math.sin(time / 4);
      this.mesh.rotation.y = Math.sin(time / 2);
      let i = 0;
      let offset = (amount - 1) / 2;

      for (let x = 0; x < amount; x++) {
        for (let y = 0; y < amount; y++) {
          for (let z = 0; z < amount; z++) {
            this.dummy.position.set(offset - x, offset - y, offset - z);
            this.dummy.rotation.y = (Math.sin(x / 4 + time) + Math.sin(y / 4 + time) + Math.sin(z / 4 + time));
            this.dummy.rotation.z = this.dummy.rotation.y * 2;
            let radioScale = Math.random();
            this.dummy.scale.set(radioScale, radioScale, Math.random());
            this.dummy.updateMatrix();
            this.mesh.setMatrixAt(i++, this.dummy.matrix);
          }
        }
      }
      this.mesh.instanceMatrix.needsUpdate = true;

      this.loading = false;

      let geometryByteLength = this.getGeometryByteLength(geometry);
      this.$message([
        'Number of Geometry: ' + String(count),
        'GPU draw calls: 1',
        'GPU memory: ' + this.formatBytes(count * 16 + geometryByteLength, 2)
      ].join('\n'));
    },
    loadMultiInstancedObject(){
      const urlParams = new URLSearchParams(window.location.search);
      const cntp = urlParams.get('count');

      let count = parseInt(cntp) || 10000;
      let scene = this.scene;

      let geometries = [
        new THREE.CylinderBufferGeometry(0.2, 0.2, 1.5, 10, 10),
          new THREE.SphereBufferGeometry(0.5)
      ]

      function addNewInstanceGeometry(geometry){
        let material = new THREE.MeshPhysicalMaterial({color: 0x777777})
        // let material = new THREE.MeshNormalMaterial();
        // let mesh = new THREE.Mesh(geometry, material);

        let mesh = new THREE.InstancedMesh(geometry, material, count);
        mesh.instanceMatrix.setUsage(THREE.StaticReadUsage);

        mesh.position.set(0, 0, 0);
        mesh.rotation.set(0, -Math.PI / 2, 0);
        mesh.scale.set(1, 1, 1);

        mesh.castShadow = true;
        mesh.receiveShadow = true;

        scene.add(mesh);

        let amount = Math.ceil(Math.pow(count, 1 / 3));

        let time = Date.now() * 0.01;
        mesh.rotation.x = Math.sin(time / 4);
        mesh.rotation.y = Math.sin(time / 2);
        let i = 0;
        let offset = (amount - 1) / 2;

        let dummy = new THREE.Object3D()

        for (let x = 0; x < amount; x++) {
          for (let y = 0; y < amount; y++) {
            for (let z = 0; z < amount; z++) {
              dummy.position.set(offset - x, offset - y, offset - z);
              dummy.rotation.y = (Math.sin(x / 4 + time) + Math.sin(y / 4 + time) + Math.sin(z / 4 + time));
              dummy.rotation.z = dummy.rotation.y * 2;
              let radioScale = Math.random();
              dummy.scale.set(radioScale, radioScale, Math.random());
              dummy.updateMatrix();
              mesh.setMatrixAt(i++, dummy.matrix);
            }
          }
        }
        mesh.instanceMatrix.needsUpdate = true;
        return mesh;
      }

      // let material = new THREE.MeshPhongMaterial({color: 0x777777, specular: 0, shininess: 20});

      geometries.forEach((ele, i, array) => {
        let mesh = addNewInstanceGeometry(ele);
      })
      this.loading = false;

      let geometryByteLength = 0;

      geometries.forEach((ele, i, array) => {
        geometryByteLength += this.getGeometryByteLength(ele)
      })

      this.$message([
        'Number of Geometry: ' + String(count),
        'GPU draw calls: ' + String(geometries.length),
        'GPU memory: ' + this.formatBytes(count * 16 + geometryByteLength, 2)
      ].join('\n'));
    },
    loadManualInstancedObject() {
      this.camera.position.set(3, 0.15, 3);
      let geometry = new THREE.CylinderBufferGeometry(0.2, 0.2, 1.5, 10, 10);
      let material = new THREE.MeshPhysicalMaterial({color: 0x777777})

      this.mesh = new THREE.InstancedMesh(geometry, material, 2);
      this.mesh.instanceMatrix.setUsage(THREE.DynamicDrawUsage);

      this.mesh.position.set(0, 0, 0);
      this.mesh.rotation.set(0, -Math.PI / 2, 0);
      this.mesh.scale.set(1, 1, 1);

      let m1 = new THREE.Matrix4().identity();
      let m2 = new THREE.Matrix4().set(
          1, 0, 0, 1,
          1, 1, 0, 1,
          0, 1, 1, 0,
          0, 0, 0, 1
      );

      this.mesh.setMatrixAt(0, m1);
      this.mesh.setMatrixAt(1, m2);

      this.mesh.instanceMatrix.needsUpdate = true;

      this.mesh.castShadow = true;
      this.mesh.receiveShadow = true;

      this.scene.add(this.mesh);
      this.loading = false;
    },
    createLights() {
      // this.scene.add(new THREE.HemisphereLight(0x443333, 0x111122));
      this.scene.add(new THREE.AmbientLight(0x606060));
      this.addShadowedLight(-1, 1, 1, 0xeeeeee, 1);
      this.addShadowedLight(-0.5, 0.5, -1, 0xcccccc, 0.5);
      this.addShadowedLight(1, 0.5, 0, 0xcccccc, 1);
    },
    addShadowedLight(x, y, z, color, intensity) {
      let directionalLight = new THREE.DirectionalLight(color, intensity);
      directionalLight.position.set(x, y, z);
      this.scene.add(directionalLight);

      directionalLight.castShadow = true;

      let d = 1;
      directionalLight.shadow.camera.left = -d;
      directionalLight.shadow.camera.right = d;
      directionalLight.shadow.camera.top = d;
      directionalLight.shadow.camera.bottom = -d;

      directionalLight.shadow.camera.near = 1;
      directionalLight.shadow.camera.far = 4;

      directionalLight.shadow.bias = -0.002;
    },
    render() {
      this.camera.lookAt(this.cameraTarget);
      this.camera.updateMatrixWorld();
      let axesPlacement = this.camera.localToWorld(new THREE.Vector3(0.45 * this.camera.aspect, -0.4, -2));
      this.axisHelper.position.copy(axesPlacement);
      this.renderer.render(this.scene, this.camera);

      // this.renderer.render(this.scene, this.camera);
    },
    onWindowResize() {
      this.camera.aspect = window.innerWidth / (window.innerHeight - 60);
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(window.innerWidth, window.innerHeight - 60);
    },
    animate() {
      requestAnimationFrame(this.animate);
      this.render();
      this.stats.update();
    },
    actionInit() {
      this.$EventBus.$on("model", (msg) => {
        switch (msg.action) {
          case "reset":
            this.camera.position.set(3, 0.15, 3);
            this.cameraTarget = new THREE.Vector3(0, -0.25, 0);
            this.$message({
              message: '复位成功',
              type: 'success'
            });
            break;
          default:
            this.$message({
              message: '收到未定义的事件: ' + msg.action,
              type: 'warning'
            });
        }
      });
    },
    getGeometryByteLength(geometry) {
      let total = 0;
      if (geometry.index) total += geometry.index.array.byteLength;
      for (let name in geometry.attributes) {
        total += geometry.attributes[name].array.byteLength;
      }
      return total;
    },
    formatBytes(bytes, decimals) {
      if (bytes === 0) return '0 bytes';
      let k = 1024;
      let dm = decimals < 0 ? 0 : decimals;
      let sizes = ['bytes', 'KB', 'MB'];
      let i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
    }
  }
}
</script>

<style scoped>

</style>



