<template>
  <VueDraggableResize :w="100" :x="20" :y="200" :resizable="false">
    <el-container class="notebook" style="height: 500px; width: 400px">
      <el-header height="30px">
        <el-row>
          <el-col :span="22" :offset="1">Pipeline</el-col>
          <el-col :span="1">
            <el-link :underline="false" type="danger" icon="el-icon-close" v-on:click="close"></el-link>
          </el-col>
        </el-row>
      </el-header>

      <el-main>
        <el-collapse v-model="activeNames" class="notebook-item-list">
          <el-collapse-item v-for="(func, index) in $store.state.notebookPipeline.pipeline" :title="func['label']"
                            :name="index" class="notebook-item">
              <el-form ref="form" :model="sizeForm" label-width="80px" size="mini">

                <el-form-item v-for="formItem in func['form']" :label="formItem['label']">
                  <el-input v-if="formItem['type'] === 'input'" :type="formItem['params']['type']"></el-input>
                  <el-upload v-else-if="formItem['type'] === 'file'" action=".">
                    <el-button size="small" type="primary">点击上传</el-button>
                    <div slot="tip" style="color: white">{{ formItem['params']['comment'] }}</div>
                  </el-upload>
                  <el-select v-else-if="formItem['type'] === 'select'" :value="formItem['params']['default']">
                    <el-option v-for="opt in formItem['params']['options']" :label="opt[1]" :value="opt[0]"></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item>
                  <el-button type="primary">应用</el-button>
                  <el-button type="danger" @click="removeFromPipeline(index)">删除</el-button>
                </el-form-item>

              </el-form>
            </el-collapse-item>
            <!--          <el-collapse-item v-for="funci in functions.length" :title="functions[funci-1]" :name="funci"  class="notebook-item">-->
            <!--            <el-form ref="form" :model="sizeForm" label-width="80px" size="mini">-->
            <!--              <el-form-item label="文件">-->
            <!--                <el-input v-model="sizeForm.name"></el-input>-->
            <!--              </el-form-item>-->
            <!--              <el-form-item label="模型">-->
            <!--                <el-upload action=".">-->
            <!--                  <el-button size="small" type="primary">点击上传</el-button>-->
            <!--                  <div slot="tip" style="color: white">只能上传stl文件</div>-->
            <!--                </el-upload>-->
            <!--              </el-form-item>-->
            <!--              <el-form-item label="算法">-->
            <!--                <el-select v-model="sizeForm.region" placeholder="算法">-->
            <!--                  <el-option label="算法一" value="alg1"></el-option>-->
            <!--                  <el-option label="算法二" value="alg2"></el-option>-->
            <!--                </el-select>-->
            <!--              </el-form-item>-->
            <!--              <el-form-item label="阈值">-->
            <!--                <el-input v-model="sizeForm.threshold" type="number"></el-input>-->
            <!--              </el-form-item>-->

            <!--              <el-form-item>-->
            <!--                <el-button type="primary">应用</el-button>-->
            <!--                <el-button type="danger">删除</el-button>-->
            <!--              </el-form-item>-->

            <!--            </el-form>-->
            <!--          </el-collapse-item>-->

        </el-collapse>
      </el-main>
    </el-container>

  </VueDraggableResize>
</template>

<script>
import VueDraggableResize from 'vue-draggable-resizable-gorkys'

export default {
  name: "Notebook",
  components: {VueDraggableResize},
  data() {
    return {
      activeNames: [],
      sizeForm: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        threshold: 0,
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
    }
  },
  methods: {
    close() {
      this.$store.commit('changeToolSideBar', {visible: false})
    },
    removeFromPipeline(index){
      this.$store.commit('removeFromPipeline', {id: index});
      this.activeNames = [];
    }
  }
}
</script>

<style>
.notebook {
  background-color: #545c64;
  border-radius: 10px;
  padding: 10px 2px 0 2px;
  text-align: center;
  color: white;

  border: solid #474747 1px;
}

.notebook-item-list {
  padding: 10px;
  max-height: 400px;
}

.notebook-item {
  background-color: #444444;
}

.el-collapse-item__header, .el-collapse-item__content {
  background-color: #5d646d;
  color: white;
}

.el-collapse-item__content {
  padding: 5px 10px 5px 1px;
  text-align: left;
}


.el-collapse-item__header {
  border: none;
  padding: 0 5px 0 5px;
}

.el-collapse-item {
  border: 1px solid #5d656d;
  border-radius: 1px;
  margin-bottom: 5px;
}

.el-collapse {
  border: none;
}

.el-main {
  padding: 0;
}
</style>


