<template>
  <VueDraggableResize :w="500" :x="20" :y="40" :resizable="false">
    <el-tabs class="function-panel" v-model="activeName">

      <el-tab-pane v-for="tab in functionList.tabs" :label="tab['label']" :name="tab['name']" class="function-panel-list">
        <el-button v-for="func in tab['functions']" type="primary" :icon="functionList.functions[func]['icon']"
          @click="addFunction(func)">
          {{ functionList.functions[func]["label"] }}
        </el-button>
      </el-tab-pane>

<!--      <el-tab-pane label="IO" name="IO" class="function-panel-list">-->
<!--        <el-button type="primary" icon="el-icon-upload">Import</el-button>-->
