<!--        <el-button type="primary" icon="el-icon-attract">View 1</el-button>-->
<!--        <el-button type="primary" icon="el-icon-umbrella">View 2</el-button>-->
<!--      </el-tab-pane>-->
<!--      <el-tab-pane label="建模" name="Modeling" class="function-panel-list">-->
<!--        <el-button type="primary" icon="el-icon-notebook-1">Modeling 1</el-button>-->
<!--        <el-button type="primary" icon="el-icon-toilet-paper">Modeling 2</el-button>-->
<!--        <el-button type="primary" icon="el-icon-school">Modeling 3</el-button>-->
<!--        <el-button type="primary" icon="el-icon-shopping-bag-1">Modeling 4</el-button>-->
<!--        <el-button type="primary" icon="el-icon-notebook-1">Modeling 5</el-button>-->
<!--        <el-button type="primary" icon="el-icon-toilet-paper">Modeling 6</el-button>-->
<!--        <el-button type="primary" icon="el-icon-school">Modeling 7</el-button>-->
<!--        <el-button type="primary" icon="el-icon-shopping-bag-1">Modeling 8</el-button>-->
<!--      </el-tab-pane>-->
<!--      <el-tab-pane label="仿真" name="Simulation" class="function-panel-list">-->
<!--        <el-button type="primary" icon="el-icon-s-promotion">Simulation 1</el-button>-->
<!--        <el-button type="primary" icon="el-icon-s-opportunity">Simulation 2</el-button>-->
<!--      </el-tab-pane>-->
    </el-tabs>
  </VueDraggableResize>
</template>

<script>
  import VueDraggableResize from 'vue-draggable-resizable-gorkys'
  import functionList from "../../functionList";

  export default {
    name: "FunctionPanel",
    components: {VueDraggableResize},
    data() {
      return {
        functionList,
        activeName: 'IO'
      };
    },
    methods: {
      addFunction(func){
        this.$store.commit('addIntoPipeline', {
          func,
          label: this.functionList.functions[func]["label"],
          form: this.functionList.functions[func]["form"]
        });
      }
    }
  }
</script>

<style>
.function-panel{
  background-color: #545c64;
  border-radius: 10px;
  padding: 10px 20px 20px 20px;
  color: white;

  border: solid #474747 1px;
}

.function-panel-list{
  text-align: left;
  white-space: nowrap;/*文本不会换行，文本会在在同一行上继续*/
  overflow-y:auto;/*可滑动*/
}

.el-form-item__label, .el-radio__label, .el-checkbox__label {
  color: white;
}

.el-tabs__item {
  color: white;
}

.el-tabs__item .el-tabs__item.is-active {
  color: white;
}
</style>


