import Vue from 'vue';
import Vuex from 'vuex';

import componentVisibility from "./componentVisibility";
import notebookPipeline from "./notebookPipeline";
import VueResource from 'vue-resource';

Vue.use(VueResource);
Vue.use(Vuex);
const store = new Vuex.Store({
    modules: {
        componentVisibility,
        notebookPipeline
    }
});

export default store;


